let lightbox = new SimpleLightbox(".gallery a", {
  captionsData: "alt",
  captionDelay: 250,
});
export function createGallery() {
  const gallery = document.querySelector(".gallery");
  if (!gallery) return;

  const markup = images
    .map(
      (img) => `
    <li class="gallery-item">
        <a class="gallery-link" href="${img.original}">
            <img
                class="gallery-image"
                src="${img.preview}"
                alt="${img.description}"
            />
        </a>
    </li>
  `
    )
    .join("");

  gallery.innerHTML = markup;


  lightbox.refresh();
}